import './App.css';
import { useEffect, useState } from 'react';
import Cards from './components/cards/cards';
import CountryPicker from './components/countryPicker/countryPicker';
import Chart from './components/chart/chart';
import { fetchData, fetchDailyData } from './api/apiData' ;


function App() {
  let [valueData, setValueData] = useState({
    data: {},
    country: '',
  });


  useEffect(() => {
    async function getData() {


      let res = await fetchData()
      setValueData({ data: res })
       console.log(valueData);
    }

    getData();
  }, [])

  const handleCountryChange = async (country) => {
    let res = await fetchData(country)
    setValueData({ data: res ,country :country })

     console.log(res);
  }


  return (

    <div className='fullForm'>
      <Cards data={valueData.data}></Cards>
      <CountryPicker handleCountryChange={handleCountryChange}></CountryPicker>

      <Chart data={valueData.data} country={valueData.country}></Chart>
    </div>

  );
}

export default App;

